<template>
    <div class="container">
        <div class="mod-calculator">
            <div class="hd"><span class="title">两者之间的称谓</span></div>
            <div class="bd">
                <div class="search">          
                    <div class="row">
                        <input type="text" placeholder="输入你亲戚的称谓" @input="bindInputTarget" v-model="target"/>
                        <template v-if="target">
                            <span class="close" @click="bindClearTarget"><img src="@/assets/icon-close.png" width="16" height="16" alt="关闭"/></span>
                        </template>
                    </div>
                    <div class="row">
                        <span class="call">⇓ 称呼</span>
                        <span class="exchange" @click="bindExchange">⇅</span>
                    </div>
                    <div class="row">
                        <input type="text" placeholder="输入你亲戚的称谓" @input="bindInputText" v-model="text"/>
                        <template v-if="text">
                            <span class="close" @click="bindClearText"><img src="@/assets/icon-close.png" width="16" height="16" alt="关闭"/></span>
                        </template>
                    </div>
                    <div class="row">
                        <button @click="bindConfirm">查找</button>
                    </div>
                </div>
                <div class="list">
                    <template v-if="text&&target">
                        <template v-if="items.length">
                            <div v-for="(item,index) in items" :key="index">
                                <div class="item">{{item}}</div>
                            </div>
                        </template>
                    </template>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Both',
        components: {
        },
        data(){
            return {
                text:'',
                target:'',
                items:[]
            }
        },
        computed: {
        },
        methods:{
            bindInputText:function(e){
                var _ = this;
                _.text = e.target.value;
                _.items = [];
            },
            bindClearText:function(){
                var _ = this;
                _.text = '';
                _.items = [];
            },
            bindInputTarget:function(e){
                var _ = this;
                _.target = e.target.value;
                _.items = [];
            },
            bindClearTarget:function(){
                var _ = this;
                _.target = '';
                _.items = [];
            },
            bindExchange:function(){
                var _ = this;
                var temp = _.text;
                _.text = _.target;
                _.target = temp;
                if(_.text&&_.target){
                    _.bindConfirm();
                }
            },
            bindConfirm:function(){
                var _ = this;
                if(_.text&&_.target){
                    var result = window.relationship({text:_.text,target:_.target});
                    if(result.length){
                        _.items = result;
                    }else{
                        _.items = ['未找到相应关系！'];
                    }
                }else{
                    alert('请输入相应称谓');
                }
            }
        },
        created(){
            
        }
    }
</script>

<style lang="scss" scoped>
    .container{
        height: 100%;
        .inner{
            height: 100%;
        }
    }
    .mod-calculator{
        height: 100%;
        background: #333;
        .hd{
            margin:  0 0.02rem;
            padding:  0.15rem 0 0.1rem;
            .title{
                display: inline-block;
                line-height: 0.22rem;
                border-left:  0.04rem solid #e7763e;
                padding-left: 0.12rem;
                font-weight: bold;
                font-size: 0.17rem;
            }
        }
        .search{
            padding: 0 0.16rem;
            margin-bottom: 0.2rem;
            width: 100%;
            box-sizing: border-box;
            .row{
                position: relative;
                width: 100%;
                margin-bottom: 0.03rem;
            }
            input{
                display: block;
                width: 100%;
                padding: 0.05rem 0.08rem;
                background: rgba(255,255,255,.1);
                border:  medium none;
                height: 0.36rem;
                line-height: 0.36rem;
                font-family: Arial,Helvetica,"Microsoft Yahei";
                font-size: 0.16rem;
                color: rgba(255,255,255,.9);
                border-radius: 0.05rem;
                box-sizing: border-box;
                outline: none;
            }
            button{
                display: block;
                width: 100%;
                height: 0.36rem;
                margin:  0.1rem 0 0.05rem;
                background: #e7763e;
                border:  medium none;
                font-size: 0.16rem;
                color: rgba(255,255,255,.9);
                border-radius: 0.05rem;
                box-sizing: border-box;
                outline: none;
                &:hover{
                    background: #e66747;
                }
            }
            .close{
                position: absolute;
                right: 0.15rem;
                top: 0.09rem;
                opacity: 0.6;
            }
            .call{
                display: inline-block;
                line-height: 0.24rem;
                color: #bbb;
            }
            .exchange{
                float: right;
                display: inline-block;
                width: 0.2rem;
                height: 0.2rem;
                margin:  0.02rem 0.1rem;
                background: #e7763e;
                line-height: 0.2rem;
                text-align: center;
                font-size: 0.16rem;
                border-radius: 50%;
                color: rgba(255, 255, 255, 0.8);
                &:hover{
                    background: #e66747;
                }
            }
        }
        .list{
            width: 100%;
            box-sizing: border-box;
            padding: 0.08rem 0.16rem;
            color: rgba(255,255,255,.9);
            .item{
                padding:  0.1rem 0.15rem;
                margin-bottom: 0.02rem;
                background: rgba(125,125,125,0.15);
                border-radius: 0.03rem;
                line-height: 0.24rem;
                font-size: 0.16rem;
            }
        }
    }
</style>